import React from 'react';
import PropTypes from 'prop-types';
import { Graph } from 'react-d3-graph';

import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  graphContainer: {
    width: '100%',
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
  },
  backdrop: {
    zIndex: 9999,
    color: '#fff',
  },
});

const GraphWorkspace = ({
  selectedGraph,
  graphConfig,
  onRightClickNode,
  onRightClickLink,
  fetching,
}) => {
  const classes = useStyles();
  const { nodes } = selectedGraph;
  if (fetching && !selectedGraph) {
    return (
      <div className={classes.graphContainer}>
        <Backdrop className={classes.backdrop} open={fetching}>
          <CircularProgress color="inherit" />
        </Backdrop>
      </div>
    );
  }
  if (!nodes.length) {
    return (
      <div className={classes.graphContainer}>
        <Typography variant="h4" color="textSecondary">
          This graph is empty
        </Typography>
        <Typography variant="subtitle1" color="textSecondary">
          You can add nodes in the panel on the right
        </Typography>
      </div>
    );
  }

  return (
    <Graph
      id="graph"
      className="Workspace__Graph"
      data={selectedGraph}
      config={graphConfig}
      onRightClickNode={onRightClickNode}
      onRightClickLink={onRightClickLink}
    />
  );
};

GraphWorkspace.defaultProps = {
  selectedGraph: {
    nodes: [],
    edges: [],
  },
  graphConfig: {
    labelProperty: 'name',
  },
  fetching: false,
};

GraphWorkspace.propTypes = {
  selectedGraph: PropTypes.shape({
    nodes: PropTypes.array,
    edges: PropTypes.array,
  }),
  graphConfig: PropTypes.shape({
    labelProperty: PropTypes.string,
  }),
  onRightClickNode: PropTypes.func.isRequired,
  onRightClickLink: PropTypes.func.isRequired,
  fetching: PropTypes.bool,
};

export default GraphWorkspace;
