import React from 'react';
import PropTypes from 'prop-types';

import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Typography from '@material-ui/core/Typography';

import './Orphanage.scss';

const Orphanage = ({
  orphans,
  spaces,
  selectedSpaceId,
  onSetSpaceSelected,
  handleOpenDialog,
  onRightClickNode,
}) => {
  return (
    <div className="Orphanage">
      <div className="Orphanage__actions">
        <Select
          labelId="Orphanage__select-space"
          className="Orphanage__select-space"
          value={selectedSpaceId || ''}
          onChange={onSetSpaceSelected}
        >
          {spaces?.map(({ id, name }) => (
            <MenuItem key={id} value={id}>
              {name}
            </MenuItem>
          ))}
        </Select>
        <Button
          className="Orphanage__button"
          onClick={handleOpenDialog}
          color="primary"
          variant="contained"
          size="large"
        >
          Add node
        </Button>
      </div>
      <div className="Orphanage__wrapper">
        <Typography className="Orphanage__title" variant="h6" noWrap>
          Orphanage
        </Typography>
        <Divider className="Orphanage__divider" />
        <List>
          {(orphans || []).map(({ id, name }) => (
            <div key={id} onContextMenu={event => onRightClickNode(event, id)}>
              <ListItem button>
                <ListItemText primary={name} />
              </ListItem>
            </div>
          ))}
        </List>
      </div>
    </div>
  );
};

Orphanage.defaultProps = {
  orphans: [],
  spaces: [],
  selectedSpaceId: '',
};

Orphanage.propTypes = {
  orphans: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
      properties: PropTypes.shape({
        name: PropTypes.string,
        space: PropTypes.string,
      }),
    })
  ),
  handleOpenDialog: PropTypes.func.isRequired,
  onRightClickNode: PropTypes.func.isRequired,
  spaces: PropTypes.arrayOf(
    PropTypes.shape({ id: PropTypes.string.isRequired, name: PropTypes.string.isRequired })
  ),
  selectedSpaceId: PropTypes.string,
  onSetSpaceSelected: PropTypes.func.isRequired,
};

export default Orphanage;
